.outerbox{
    margin-top: 350px;
    margin-bottom: 100px;
    position: relative;
    color: black;
    text-align: center;
}

.au-heading{
    font-weight: 600;
    letter-spacing: 1px;
    font-size: 1.2em;
}

.au-subheading{
    margin-top: 10px;
    font-weight: 100;
    color: rgba(0, 0, 0, 0.600);
    text-transform: uppercase;
    font-size: 0.9em;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.au-body{
    position: relative;
    margin-top: 25px;
    left: 50%;
    width: 700px;
    transform: translateX(-50%);
}

.au-body p{
    text-align: center;
}

.about-me{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 800px;
    height: 100px;
    margin-top: 50px;
    clip-path: polygon(0 0, 750px 0, 800px 100px, 50px 100px);
    background-color: rgba(255, 248, 220, 0.295);
    color: black;
    display: flex;
}

.me-photo{
    position: relative;
    float: left;
    width: 75px;
    margin-top: 3px;
    margin-left: 50px;
    border-radius: 20px;
}

.me-details{
    margin-top: 10px;
    margin-left: 20px;
    width: 600px;
    text-align: justify;
}

@media screen and (max-width:800px){
    .outerbox{
        margin-top: 250px;
        margin-bottom: 100px;
    }

    .about-me{
        width: 400px;
        height: 300px;
        clip-path: polygon(0 0, 380px 0, 400px 300px, 20px 300px);
        display: block;
    }

    .me-photo{
        margin: 0;
        margin-top: 15px;
        left: 50%;
        transform: translateX(-50%);
    }

    .me-details{
        padding-top: 120px;
        padding-left: 10px;
        padding-right: 20px;
        width: 340px;
    }

    .au-body{
        width: 600px;
    }
}

@media screen and (max-width:600px){
    .outerbox{
        margin-top: 220px;
        margin-bottom: 80px;
    }

    .au-body{
        width: 90vw;
    }
}

@media screen and (max-width:400px){
    .about-me{
        width: 90vw;
        height: 380px;
        clip-path: polygon(0 0,85vw 0, 90vw 380px,5vw 380px);
    }

    .me-details{
        width: 75vw;
        margin-left: 10px;
    }
}