.background{
    width: 100%;
    height: 100vh;
    background-image:linear-gradient(to bottom left,rgba(0, 0, 0, 0.100),rgba(0, 0, 0, 0.4)), url(../../images/background.jpg);

    z-index: 1;
}

.background-static{
    position: fixed;
    width: 100vw;
    height: 150vh;
    background-image: url(../../images/silhouttes-of-mountains-3209168.jpg);
    transform: translateY(-135vh);
    z-index: -1;
}

@media screen and (max-width:800px){
    .background{
        height: 600px;
    }

    .background-static{
        height: 150%;
    }
}
