.prof-intro{
    margin-top: 40px;
    width: 100%;
    text-align: center;
    background-image: url(../../images/blue-body-of-water-with-orange-thunder-1102915.jpg);
}

.prof-intro h3{
    padding: 60px 0 30px 0;
    color: rgba(204, 204, 204, 0.801);
}

.container{
    position: relative;
    width: 800px;
    left: 50%;
    transform: translateX(-50%);
    height: 980px;
}

.container .box{
    position: relative;
    width: 270px;
    height: 270px;
    background-color: coral;
    float: left;
    margin: 30px 60px;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
}

.container .box .icon{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
    background-color: crimson;
    z-index: 1;
}

.container .box:hover .icon{
    top: 15px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    transition: 0.4s;
}

.prof-info{
    padding: 10px;
    padding-top: 120px;
}

.last{
    margin: 0;
    left: 50%;
    transform: translateX(-50%);
}

.p-name{
    text-align: center;
    margin-bottom: 7px;
}

.info{
    font-weight: 200;
    text-align: justify;
}

@media screen and (max-width:800px){
    .container{
        width: 600px;
        height: 900px;
        display: block;
    }
    .container .box{
        margin: 15px;
    }

    .container .box:hover .icon{
        top: 10px;
        width: 80px;
        height: 80px;
    }

    .prof-info{
        padding: 8px;
        padding-top: 95px;
    }
}

@media screen and (max-width:600px){
    .container{
        width: 320px;
        height: 1150px;
        display: block;
    }

    .container .box{
        width: 200px;
        height: 200px;
        margin: 15px 60px;
    }

    .container .box:hover .icon{
        width: 60px;
        height: 60px;
    }

    .prof-info{
        padding: 5px;
        padding-top: 70px;
        font-size: 0.8em;
    }

    .last{
        left: 0;
        transform: translateX(0);
    }
}