

.s-prof{
    position: relative;
    width: 600px;
    padding: 80px 10px 0 10px;
    display: flex;
    left: 50%;
    transform: translateX(-50%);
    align-items: center;
}

.s-prof p{
    margin-left: 80px;
    color: ivory;
    font-size: 1.5em;
}

.prof-img{
    position: relative;
    width: 100px;
    border-radius: 10px;
    margin-left: 100px;
}

.s-head{
    padding-top: 20%;
    transform: translateY(-50%);
}

.subject-heading{
    width: 100%;
    text-align: center;
    font-size: 2em;
    color: white;
    z-index: 10;
}

.sub-students{
    margin-top: 100px;
    text-align: center;
}

.students-head{
    letter-spacing: 2px;
    font-size: 1.3em;
}

.students-list{
    margin-top: 30px;
    padding: 10px;
    position: relative;
    width: 620px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(240, 230, 140, 0.5);
}

.students-list ul{
    display: block;
}

.students-list ul li{
    display: flex;
}

.roll{
    width: 150px;
    border-left: solid;
    border-right: solid;
    text-align: center;
    padding: 10px 0;
    border-bottom: solid;
}

.name{
    width: 300px;
    border-left: solid;
    border-right: solid;
    text-align: center;
    padding: 10px 0;
    border-bottom: solid;
}

.remove{
    width: 150px;
    border-left: solid;
    border-right: solid;
    text-align: center;
    padding: 10px 0;
    border-bottom: solid;
}

.remove-btn{
    padding: 5px;
    background-color: lightcoral;
    border-radius: 5px;
    cursor: pointer;
}

.table-head{
    border-style: solid;
    padding: 20px 0;
}

.add-btn{
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 7px 25px;
    background-color: rgba(255, 0, 0, 0.5);
    cursor: pointer;
}

@media screen and (max-width:800px){
    .s-prof{
        padding-top: 120px;
        width: 380px;
    }
    
    .s-prof p{
        margin-left: 10px;
    }
    
    .prof-img{
        margin-left: 10px;
    }
    .s-head{
        padding-top: 70px;
        transform: translate(0);
    }
    .sub-students{
        margin-top: 200px;
    }
}

@media screen and (max-width:650px){
    .students-list{
        width: 400px;
    }
}

@media screen and (max-width:420px){
    .s-prof{
        width: 300px;
    }

    .s-head{
        padding-top: 90px;
        transform: translate(0);
    }
    
    .s-students{
        margin-top: 150px;
    }

    .students-list{
        width: 300px;
    }
}