.footer-str{
    width: 100%;
    height: 80px;
    background-color: rgb(221, 220, 220);
    display: flex;
}

.footer-str div{
    padding: 0 20px;
}

.footer-content{
    display: flex;
    align-items: center;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}

.favicon{
    position: relative;
    width: 30px;
}