.subject-intro{
    padding-top: 70px;
    width: 100%;
    text-align: center;
    background-image: url(../../images/background.jpg);
    display: flex;
}

.subject-container{
    position: relative;
    width: 800px;
    left: 50%;
    transform: translateX(-50%);
    display: inline;
}

.container .flip-card{
    position: relative;
    width: 200px;
    height: 220px;
    float: left;
    margin: 30px 100px;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
}


.flip-card {
    background-color: transparent;
    width: 200px;
    height: 220px;
    perspective: 1000px;

    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
}

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.subject-img{
    width: 200px;
    height: 220px;
}
  
.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
  

.flip-card-front {
    background-color: #bbb;
    color: black;
}

.flip-card-back {
    padding-top: 15px;
    background-color: dodgerblue;
    color: white;
    transform: rotateY(180deg);
}
  
.last-card{
    left: 50%;
    transform: translateX(-100%);
}

.sub-btn{
    color: rgba(44, 44, 44, 0.8);
    font-weight: 700;
    background-color: whitesmoke;
    padding: 5px 10px;
    border-radius: 5px;
}


@media screen and (max-width:800px){
    .container{
        width: 600px;
        height: 900px;
        display: block;
    }

    .container .flip-card{
        margin: 15px 50px;
    }

    .last-card{
        transform: translateX(-75%);
    }
}

@media screen and (max-width:600px){
    .container{
        width: 320px;
        height: 1200px;
        display: block;
    }

    .container .flip-card{
        width: 200px;
        height: 200px;
        margin: 15px 60px;
    }

    .last-card{
        left: 0;
        transform: translateX(0);
    }
}