*{
    margin: 0;
    padding: 0;
}

.title{
    padding: 5px 20px;
    background-color: white;
    color: rgba(0, 131, 114, 0.76);
    font-weight: 700;
    font-size: 1.0em;
    margin-left: 0px;

}

.nav-contents{
    width: 100%;
    justify-content: space-around;
    height: 8vh;
    background-color: rgba(0, 255, 255, 0.8);
    align-items: center;
    display: flex;
    position: fixed;
    z-index: 10;
}

.nav{
    display: flex;
    float: right;
}

.nav li{
    list-style: none;
}

.nav li a{
    font-weight: 700;
    font-size: 0.8em;
    margin: 0 23px;
    padding: 5px;
    text-align: right;
    color: gray;
}

.nav-contents a:hover{
    color: black;
}
