.intro{
    color: ivory;
    padding-top: 100px;
    margin-left: 5vw;
    text-align: left;
    font-size: 3em;
    font-weight: 1000;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 10px;
}

.intro-text{
    color: ivory;
    margin-left: 5vw;
    margin-top: 20px;
    font-size: 1em;
    font-weight: 100;
    letter-spacing: 2px;
}

.lm-btn{
    margin-top: 30px;
    margin-left: 5vw;
}

.learn-more{
    color: black;
    padding: 10px 25px;
    font-size: 0.8em;
    background-color: white;
    font-weight: 500;
    letter-spacing: 1px;
    border-radius: 5px;
}

a{
    text-decoration: none;
}

@media screen and (max-width:800px){
    .intro{
        padding-top: 150px;
    }
}

@media screen and (max-width:600px){
    .intro{
        font-size: 1.6em;
        padding-top: 170px;
    }

    .intro-text{
        margin-top: 45px;
        font-size: 0.9em;
    }
    .lm-btn{
        margin-top: 40px;
    }
}